  width: 21px;
  height: 19px;
  margin: 0;
  padding: 0;
  border: none;
  background-color: transparent;
  justify-content: center;
  align-items: center;
  transition: 0.2s;
  appearance: none;
  cursor: pointer; }
  .burger span {
    width: 100%;
    height: 3px;
    background-color: #446cb2;
    transition: 0.2s; }
    .burger span::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      display: block;
      width: 100%;
      height: 3px;
      background-color: #446cb2;
      transition: 0.2s; }
    .burger span::after {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      display: block;
      width: 100%;
      height: 3px;
      background-color: #446cb2;
      transition: 0.2s; }
  .burger:hover {
    opacity: 0.7; }
  .burger.is-active span {
    background-color: transparent; }
    .burger.is-active span:before {
      top: 50%;
      transform: translateY(-50%) rotate(45deg); }
    .burger.is-active span:after {
      bottom: 50%;
      transform: translateY(50%) rotate(-45deg); }

